.about {
  background-color: #fde2e2;
  min-height: 100vh;
  min-width: 450px;
  height: auto;
  display: flex;
  flex-direction: column;
  padding-bottom: 10vh;
}
.about__title {
  font-size: 1rem;
  text-align: left;
  padding-left: 8vw;
  padding-top: 10vh;
  word-spacing: 10px;
  letter-spacing: 2px;
}
.about__content{
    padding-left: 15vw;
    padding-right: 15vw;
    padding-top: 5vh;
}

.about__list{
  padding-top: 5vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: space-evenly;
}